___CSS_LOADER_EXPORT___.push([module.id, `#calc {
    background-color: plum;
    border:solid;
    border-radius:25px;
    padding:10px;
    margin-left:auto;
    margin-right:auto;
}




#calc input[type="button"] {
    background-color: grey;
    color: white;
    height:50px;
    width:100px;
    border-radius:25px;
}

#calc input[type="button"][value="="],#calc input[type="button"][value="C"]{
    background-color:rgb(251, 147, 136);
}



#result{
    padding:10px;
    width:285px;
    height:25px;
}

#calc input[type="button"]:hover{
    cursor:pointer;
}

